export default {
  qwerty: [
    "` 1 2 3 4 5 6 7 8 9 0 - = {backspace:label=}",
    "{tab} q w e r t y u i o p [ ] \\",
    "{caps} a s d f g h j k l ; ' {enter}",
    "{shift} z x c v b n m , . / {shift}",
    "{empty} {empty} {empty} {space} {empty} {empty} {empty}"
  ],
  hebrew: [
    "; 1 2 3 4 5 6 7 8 9 0 - = {backspace:label=}",
    "{tab} / '         ] [ {empty}",
    "{caps}           , \\ {empty}",
    "{shift}          . {shift}",
    "{empty} {empty} {empty} {space} {empty} {empty} {empty}"
  ]
};



